
import VueDatePicker from '@vuepic/vue-datepicker';
import Pagination from '@/shared/components/Pagination/Pagination.vue'
import '@vuepic/vue-datepicker/dist/main.css'
import { defineComponent } from "vue";
import { PAGE_SIZE } from "@/shared/constants/common.const";
import { ICM21100111_RES, ICM21100111_REQ, PromotionList } from "@/shared/type/ICM21100111.type";
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import SelectOption from "@/shared/components/SelectOption/SelectOption.vue";

export default defineComponent({
    name: "icm211001sc00",

    components: {
        VueDatePicker,
        Pagination,
        BreadCrumb,
        SelectOption
    },
    data() {
        const header = [
            { columnName: this.$t('icm211001sc00.column.no') },
            { columnName: this.$t('icm211001sc00.column.view') },
            { columnName: this.$t('icm211001sc00.column.title') },
            { columnName: 'Status' },
            { columnName: this.$t('icm211001sc00.column.uploader') },
            { columnName: this.$t('icm211001sc00.column.created_date') },
            { columnName: this.$t('icm211001sc00.column.display') },
        ];
        const searchForm = {
            displayYN: '',
            searchValue: '',
            statusCode: '',
            publishToDate: new Date(),
            publishFromDate: new Date()
        }
        const promotionList = [] as PromotionList[];
        const seletedButton = '02' as string
        const totalItems = 0 as number
        const currentPage = 1 as number
        const pageSize = PAGE_SIZE as number
        const startingIndex = 1 as number
        const isReset = false as boolean

        return {
            header,
            promotionList,
            searchForm,
            seletedButton,
            totalItems,
            currentPage,
            pageSize,
            startingIndex,
            isReset
        };
    },

    computed: {
        formattedStartDate(): string {
            return this.$datetime.formatVueDatePicker(this.searchForm.publishFromDate)
        },
        formattedEndDate(): string {
            return this.$datetime.formatVueDatePicker(this.searchForm.publishToDate)
        },

        displayYN() {
            return this.$store.state.CommonCodeModule.commonCode.YnType
        },
    },

    mounted() {
        this.getSelcetedDate()
        this.getPromotionList()
    },

    methods: {
        async getPromotionList() {
            let reqBody = {
                publishToDate: this.$datetime.convertDateToString(this.searchForm.publishToDate),
                pageSize: this.pageSize,
                displayYN: this.searchForm.displayYN,
                pageNumber: this.currentPage,
                statusCode: this.searchForm.statusCode,
                searchValue: this.searchForm.searchValue,
                publishFromDate: this.$datetime.convertDateToString(this.searchForm.publishFromDate),
            } as ICM21100111_REQ;
            const res = await this.$request.getRequest("ICM21100111", reqBody, false) as ICM21100111_RES;
            this.promotionList = res?.body?.list.map((item: PromotionList) => {
                return {
                    title: item.title,
                    description: item.description,
                    uploader: item.firstRegisterTellerName && item.firstRegisterTellerID ? `${item.firstRegisterTellerName} (${item.firstRegisterTellerID})` : "",
                    firstRegisterDate: this.$datetime.formatStringToStringDate(item.firstRegisterDate),
                    displayYN: this.$codeConverter.convertValue("YnType", item.displayYN),
                    firstRegisterTellerID: item.firstRegisterTellerID,
                    promotionID: item.promotionID,
                    firstRegisterTellerName: item.firstRegisterTellerName,
                    statusCode: `${item.statusCode} : ${this.$codeConverter.convertValue("ContentManangementStatusCode", item.statusCode)}`,
                }
            });
            this.totalItems = res.body.totalCount;
        },

        handleClickAction(actionType: string) {
            switch (actionType) {
                case 'register':
                    this.$router.push({
                        path: `ICM211001SC01`,
                        query: { type: "register", }
                    });
                    break;
                case 'search':
                    if (this.currentPage > 1) {
                        this.currentPage = 1
                        this.isReset = true
                        this.getPromotionList();
                    } else {
                        this.getPromotionList();
                    }
                    break;
                default: //case reset
                    this.searchForm.statusCode = this.searchForm.searchValue = this.searchForm.displayYN = ''
                    this.isReset = true
                    this.currentPage = 1
                    this.getSelcetedDate()
                    this.getPromotionList()
                    break;
            }
        },

        previousMonth(date: Date, monthIndex: number) {
            let year = date.getFullYear()
            let month = date.getMonth() - monthIndex
            let day = date.getDate()
            return new Date(year, month, day)
        },

        getSelcetedDate() {
            this.seletedButton = "02";
            this.searchForm.publishToDate = new Date();
            this.searchForm.publishFromDate = new Date(new Date().setDate(this.searchForm.publishToDate.getDate() - 7))
        },

        buttonActionClick(actionType: string) {
            this.searchForm.publishToDate = new Date()
            switch (actionType) {
                case 'today':
                    this.searchForm.publishFromDate = new Date()
                    this.seletedButton = '01'
                    break
                case '1week':
                    this.searchForm.publishFromDate = new Date(new Date().setDate(this.searchForm.publishToDate.getDate() - 7))
                    this.seletedButton = '02'
                    break
                case '1month':
                    this.searchForm.publishFromDate = new Date(this.previousMonth(this.searchForm.publishToDate, 1))
                    this.seletedButton = '03'
                    break
                case '3months':
                    this.searchForm.publishFromDate = new Date(this.previousMonth(this.searchForm.publishToDate, 3))
                    this.seletedButton = '04'
                    break
                case '6months':
                    this.searchForm.publishFromDate = new Date(this.previousMonth(this.searchForm.publishToDate, 6))
                    this.seletedButton = '05'
                    break
            }
            this.getPromotionList();
        },

        handleRowClick(item) {
            this.$router.push({
                path: `ICM211001SC01`,
                query: {
                    type: "edit",
                    promotionID: item.promotionID
                }
            });
        },

        handlePagination(pageNumber: number) {
            this.isReset = false
            this.currentPage = pageNumber
            this.getPromotionList()
        },

        handleStartingIndex(startingIndex: number) {
            this.startingIndex = startingIndex
        },

        handleChange(option: string) {
            if (option === '01') {
                this.seletedButton = 'abc'
            } else {
                this.seletedButton = 'ccc'
            }
        }
    },
});
